import React from "react";
import { useLocation, useNavigate } from "react-router-dom";
import {
  Box,
  Drawer,
  useMediaQuery,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
} from "@mui/material";
import { SidebarWidth } from "../../../assets/global/Theme-variable";
import LogoIcon from "../Logo/LogoIcon";
import Menuitems from "./data";

const Sidebar = (props) => {
  const { pathname } = useLocation();
  const navigate = useNavigate();
  const lgUp = useMediaQuery((theme) => theme.breakpoints.up("lg"));

  const handleNavigation = (href) => {
    // First close the mobile sidebar if needed
    if (!lgUp) {
      props.onSidebarClose?.();
    }
    // Then navigate
    setTimeout(() => {
      navigate(href);
    }, 0);
  };

  const SidebarContent = (
    <Box sx={{ p: 3, height: "calc(100vh - 40px)" }}>
      <Box 
        component="div"
        role="button"
        onClick={() => handleNavigation("/app/dashboards/dashboard1")}
        sx={{ 
          display: "flex", 
          alignItems: "Center", 
          cursor: "pointer",
          '&:hover': {
            opacity: 0.8
          }
        }}
      >
        <LogoIcon />
      </Box>

      <List sx={{ mt: 4 }}>
        {Menuitems.map((item) => {
          const isActive = pathname === item.href;
          return (
            <ListItem
              key={item.title}
              component="div"
              role="button"
              onClick={() => handleNavigation(item.href)}
              sx={{
                mb: 1,
                cursor: 'pointer',
                transition: 'all 0.2s',
                '&:hover': {
                  backgroundColor: (theme) => 
                    isActive 
                      ? theme.palette.primary.main
                      : theme.palette.action.hover,
                },
                ...(isActive && {
                  backgroundColor: (theme) =>
                    `${theme.palette.primary.main}!important`,
                  color: "white",
                }),
              }}
            >
              <ListItemIcon
                sx={{
                  minWidth: '40px',
                  ...(isActive && { color: "white" }),
                }}
              >
                <item.icon />
              </ListItemIcon>
              <ListItemText 
                primary={item.title}
                sx={{
                  '& .MuiTypography-root': {
                    fontWeight: isActive ? 600 : 400,
                  }
                }}
              />
            </ListItem>
          );
        })}
      </List>
    </Box>
  );

  if (lgUp) {
    return (
      <Drawer
        anchor="left"
        open={props.isSidebarOpen}
        variant="persistent"
        PaperProps={{
          sx: {
            width: SidebarWidth,
            border: "0 !important",
            boxShadow: "0px 7px 30px 0px rgb(113 122 131 / 11%)",
          },
        }}
      >
        {SidebarContent}
      </Drawer>
    );
  }

  return (
    <Drawer
      anchor="left"
      open={props.isMobileSidebarOpen}
      onClose={props.onSidebarClose}
      PaperProps={{
        sx: {
          width: SidebarWidth,
          border: "0 !important",
        },
      }}
      variant="temporary"
    >
      {SidebarContent}
    </Drawer>
  );
};

export default Sidebar;
